import { ConnectorRuntimeSDK } from '@transai/connector-runtime-sdk';
import { OpcuaClient } from '@transai/opcua-client';

import { ExtractorService } from './extractor.service';
import { ResultHandler } from './result.handler';
import { ConnectorConfig } from './types';

export class ConnectorRunnerBystronic extends ConnectorRuntimeSDK<ConnectorConfig> {
  readonly #OPCUA_NAMESPACE = 'http://bystronic.com/ByVisionCutting/';

  override init = async (): Promise<void> => {
    const { config } = this.connectorSDK;

    const opcUaClient = new OpcuaClient({
      namespace: this.#OPCUA_NAMESPACE,
      ...this.connectorSDK.config.opcuaConfig,
    });
    const opcUaResultHandler = new ResultHandler(
      this.connectorSDK,
      opcUaClient,
    );

    config.opcUaCalls.forEach((opcUaCallConfig) => {
      this.connectorSDK.processing.registerInterval(
        opcUaCallConfig.interval,
        new ExtractorService(
          this.connectorSDK,
          opcUaCallConfig,
          opcUaClient,
          opcUaResultHandler,
        ),
      );
    });
  };
}
